<template>
  <div>
    <div class="topbar_o">
      <div class="container">
        <div class="topInner">
          <template v-if="User">
            <template v-if="User.Type == 1">
              <div class="tSpaceLink">
                <span>教師專區</span>
                <ul class="dDown-menu">
                  <li>
                    <router-link to="/teacher/class/my">我的班級</router-link>
                  </li>
                  <li>
                    <router-link to="/teacher/class/create"
                      >建立班級</router-link
                    >
                  </li>
                  <li>
                    <router-link to="/teacher/class/trace"
                      >學習進度追蹤</router-link
                    >
                  </li>
                </ul>
              </div>
              <div class="loggedInUser">
                <span>Hi, {{ User.Name }}老師</span>
                <ul class="dDown-menu">
                  <li><router-link to="/history">觀看紀錄</router-link></li>
                  <li><router-link to="/profile">編輯個人資料</router-link></li>
                  <li>
                    <a href="javascript:void(0);" @click="logout">登出</a>
                  </li>
                </ul>
              </div>
            </template>
            <template v-else-if="User.Type == 2">
              <div class="tSpaceLink">
                <span>學生專區</span>
                <ul class="dDown-menu">
                  <li><router-link to="/student/class/my">我的課程</router-link></li>
                  <li><router-link to="/student/class/add">加入課程</router-link></li>
                  <li><router-link active-class="act" to="/student/history">上課紀錄</router-link></li>
                </ul>
              </div>
              <div class="loggedInUser">
                <span>Hi, {{ User.Name }}您好</span>
                <ul class="dDown-menu">
                  <li><router-link to="/profile">編輯個人資料</router-link></li>
                  <li>
                    <a href="javascript:void(0);" @click="logout">登出</a>
                  </li>
                </ul>
              </div>
            </template>
            <template v-else>
              <router-link to="/history">觀看紀錄</router-link>
              <div class="loggedInUser">
                <span>Hi, {{ User.Name }}您好</span>
                <ul class="dDown-menu">
                  <li><router-link to="/profile">編輯個人資料</router-link></li>
                  <li>
                    <a href="javascript:void(0);" @click="logout">登出</a>
                  </li>
                </ul>
              </div>
            </template>
          </template>
        </div>
      </div>
    </div>
    <header>
      <div class="container">
        <div class="logo">
          <router-link to="/"><img src="/img/logo.png" /></router-link>
        </div>
        <div class="main-menu">
          <ul>
            <li>
              <router-link to="/category/all">學生課程</router-link>
              <ul class="sub-menu">
                <li><router-link to="/category/all">全部</router-link></li>
                <li
                  v-for="c in LessonCatrgories"
                  :key="c.LessonCategoryID"
                  v-show="c.LessonCategoryID > 1"
                >
                  <router-link :to="'/category/' + c.LessonCategoryID">{{
                    c.CategoryName
                  }}</router-link>
                </li>
              </ul>
            </li>
            <li><router-link to="/category/1">師培課程</router-link></li>
            <li><router-link to="/techplans">教學資源</router-link></li>
            <li v-if="!User"><router-link to="/login">登入</router-link></li>
            <li v-if="!User"><router-link to="/register">註冊</router-link></li>
            <li class="zoom">
              <a href="javascript:void(0);" @click="showSearch"
                ><img src="/img/zoom-w.png"
              /></a>
            </li>
          </ul>
          <div class="h_search">
            <input type="text" placeholder="搜尋更多課程" v-model="Keywrod"  v-on:keyup.enter="search" /><a
              href="javascript:void(0);"
              @click="search"
              ><img src="/img/send.svg"
            /></a>
          </div>
         
        </div>
         <div class="menu_burger">
            <span></span>
            <span></span>
            <span></span>
          </div>
      </div>
    </header>
  </div>
</template>
<script>
export default {
  data() {
    return {
      User: null,
      LessonCatrgories: [],
      Keywrod: "",
    };
  },
  async mounted() {
    this.setBase("home-wide");
    this.LessonCatrgories = await this.$api.getLessonCategories();
    this.User = this.$user.user;
    this.$user.addOnUpdateHandler((user) => {
      this.User = user.user;
    });
    this.$nextTick(()=>{
      $(".menu_burger").click(function () {
        if ($(".main-menu>ul").hasClass("show")) {
          $(".main-menu>ul").removeClass("show");
          $(".menu_burger").removeClass("on");
          $(".h_search").removeClass("act");
        } else {
          $(".main-menu>ul").addClass("show");
          $(".menu_burger").addClass("on");
        }
        
      });


      $(".menu_burger, header, .main").click(function () {
        
        $(".tSpaceLink .dDown-menu").removeClass("show");
        $(".sSpaceLink .dDown-menu").removeClass("show");
        $(".loggedInUser .dDown-menu").removeClass("show");
        
      });
        $(".main-menu li").click(function () {
          if ($(".main-menu>ul").hasClass("show")) {
            $(".main-menu>ul").removeClass("show");
            $(".menu_burger").removeClass("on");
          } 
        });
      $(".main-menu li:not('.zoom'), .h_search img").click(function () {
          $(".main-menu>ul").removeClass("show");
          $(".menu_burger").removeClass("on");
          $(".h_search").removeClass("act");
      });



      $(".loggedInUser > span").click(function () {
        if ($(".loggedInUser .dDown-menu").hasClass("show")) {
          $(".loggedInUser .dDown-menu").removeClass("show");
        } else {
          $(".loggedInUser .dDown-menu").addClass("show");
        }

          $(".tSpaceLink .dDown-menu").removeClass("show");
      });

      $(".tSpaceLink > span").click(function () {

          $(".loggedInUser .dDown-menu").removeClass("show");
        if ($(".tSpaceLink .dDown-menu").hasClass("show")) {
          $(".tSpaceLink .dDown-menu").removeClass("show");
        } else {
          $(".tSpaceLink .dDown-menu").addClass("show");
        }

      });

      $(".sSpaceLink > span").click(function () {
        if ($(".sSpaceLink .dDown-menu").hasClass("show")) {
          $(".sSpaceLink .dDown-menu").removeClass("show");
        } else {
          $(".sSpaceLink .dDown-menu").addClass("show");
        }
          $(".loggedInUser .dDown-menu").removeClass("show");
      });

      $(".topbar_o .dDown-menu a").click(function () {
          $(".dDown-menu").removeClass("show");
      });
    });
  },
  methods: {
    logout() {
      this.$user.logout();
      this.User = null;
      this.$router.replace('/');
    },
    showSearch() {
      if ($(".h_search").hasClass("act")) {
        $(".h_search").removeClass("act");
      } else {
        $(".h_search").addClass("act");
      }
    },
    search() {
      if (this.Keywrod) {
        this.$router.push("/search/" + encodeURIComponent(this.Keywrod));
      }
    },
  },
};

</script>