<template>
  <div v-show="Ready">
    <div class="sec2">
      <div class="slideLogin">
        <div class="mainSlide">
          <div class="slide" v-for="b in Banner" :key="b.BannerID">
            <a :href="b.TargetUrl" target="_blank"><img :src="b.ImageUrl | imageCDN"  /></a>
          </div>
        </div>
      </div>
    </div>
    <div class="main homeContent">
      <div class="row new">
        <div class="container">
          <div class="subTitle">
            <h3>
              <img src="/img/latestTitle.png" alt="" />
            </h3>
          </div>
          <LessonSlider :loader="loadNew" :key="New.length" />
          <router-link class="moreCourse" to="/category/all">更多課程
            <img src="/img/arrowx3.svg">
          </router-link>
        </div>
      </div>
      <div class="middleWidget">
        <div class="container">
          <div class="socialLinks">
            <a :href="Setting.YouTube" target="_blank"
              ><img src="/img/icon-YT.svg" /></a
            ><a :href="Setting.Facebook" target="_blank"
              ><img src="/img/icon-FB.svg" /></a
            ><a :href="Setting.Instagram" target="_blank"
              ><img src="/img/icon-IG.svg"
            /></a><a :href="Setting.Podcast" target="_blank"
              ><img src="/img/icon-podcast.svg"
            /></a>
          </div>
          <div class="searchColumn">
            <div class="searchFrame">
              <input
                type="text"
                placeholder="搜尋更多課程"
                v-model="Keywrod"
                v-on:keyup.enter="search"
              /><a
                class="searchSubmit"
                href="javascript:void(0);"
                @click="search"
                ><img src="/img/zoom-blue.svg"
              /></a>
            </div>
          </div>
        </div>
      </div>
      <div class="row hot">
        <div class="container">
          <div class="subTitle">
            <h3>
              <img src="/img/hotTitle.png" alt="" />
            </h3>
          </div>
          <LessonSlider :loader="loadHot" :key="Hot.length" />
            <router-link class="moreCourse" to="/category/all">更多課程
            <img src="/img/arrowx3.svg">
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LessonSlider from "@/components/LessonSlider.vue";
export default {
  components: {
    LessonSlider,
  },
  data() {
    return {
      Banner: [],
      Ready: false,
      Keywrod: "",
      New: [],
      Hot: [],
      Setting: {
        YouTube: "",
        Facebook: "",
        Instagram: "",
      },
    };
  },
  async mounted() {
    this.setBase("home-wide unlogin");

    var response = await this.$api.getHome();
    this.Banner = response.Banner;
    this.New = response.New;
    this.Hot = response.Hot;
    if (response.Setting) {
      this.Setting = $.extend(this.Setting, JSON.parse(response.Setting));
    }
    this.$nextTick(() => {
      $(".mainSlide").slick({
        arrows: false,
        autoplay: true,
        dots: true,
        //- adaptiveHeight: true
      });
    });

    this.Ready = true;
    this.loadCustomJs();
  },
  methods: {
    search() {
      if (this.Keywrod) {
        this.$router.push("/search/" + encodeURIComponent(this.Keywrod));
      }
    },
    loadNew() {
      return new Promise((resolve, reject) => resolve(this.New));
    },
    loadHot() {
      return new Promise((resolve, reject) => resolve(this.Hot));
    },
    async loadCustomJs() {
      try {
        await $.getScript(window.filters.imageCDN("custom.js"));
      } catch (e) {}
    },
  },
};
</script>
